import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { GlobalVarsProvider } from '../../providers/global-vars/global-vars';
/**
 * Generated class for the ConfirmTransPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-confirm-trans',
  templateUrl: 'confirm-trans.html',
})
export class ConfirmTransPage {
  langs: any;
  constructor( public globalVars: GlobalVarsProvider,public navCtrl: NavController, public navParams: NavParams ) {
    this.langs = this.globalVars.doTranslate( this.globalVars.getLang() );
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ConfirmTransPage');
  }

}



